import React, { Component } from 'react'

import ButtonBackToHome from '../Atoms/ButtonBackToHome'


const API_KEY = 'e2c683b0'
class Details extends Component {

    state = {
        movie: {}
    }

    fetchMovie({ id }) {
        fetch(`https://www.omdbapi.com/?apikey=${API_KEY}&i=${id}`)
            .then(res => res.json())
            .then(movie => {
                this.setState({ movie })
            })

    }

    componentDidMount() {
        const { id } = this.props.match.params
        this.fetchMovie({ id })
    }

    render() {
        const movie = this.state.movie
        return (
            <div>
                <ButtonBackToHome />
                <h1>{movie.Title}</h1>
                {
                    movie.Poster
                        ? <img src={movie.Poster} alt={movie.Title}></img>
                        : <img src='data:image/jpeg;base64,'
                            alt={movie.Title}></img>
                }



                <div className="column is-narrow">
                    <p className="bd-notification is-primary">
                        <code className="html">Metascore</code><br />
                        <code className="html">{movie.Metascore}</code>

                    </p>
                </div><div className="column is-narrow">
                    <p className="bd-notification is-primary">
                        <code className="html">Actors</code><br />
                        <code className="html">{movie.Actors}</code>
                    </p>
                </div>
                <div className="columns is-mobile is-centered">
                    <div className="column is-half">
                        <p className="bd-notification is-primary">
                            <code className="html">Plot</code><br />
                            <code className="html">{movie.Plot}</code><br />
                        </p>
                    </div>
                </div>
            </div>
        )
    }

}

export default Details;